@import 'main';
// End Of Import

.navover{
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, .8);
    z-index: 9999;
    opacity: 0;
    visibility: hidden;
    transition: .5s;

    &.open{
        opacity: 1;
        visibility: visible;
    }
}

.sideNav{
    padding: 24px;
    position: fixed;
    top: 0;
    right: -100%;
    width: 260px;
    height: 100%;
    background-color: #FFF;
    z-index: 9999;
    overflow: auto;
    transition: .5s;

    &.open {
        right: 0;
    }

    .opt{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;

        .lang{


            a{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 30px;
                height: 30px;
                color: #000;
                font-size: 15px;
            }
        }

        .xtr{
            position: relative;
            width: 30px;
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: auto;
            margin-right: 10px;

            .srch{
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;

                svg{

                    path{
                        fill: #000;
                    }
                }
            }

            form{
                opacity: 0;
                visibility: hidden;
                z-index: 99;
                position: fixed;
                top: 0;
                right: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(0,0,0, .9);
                display: flex;
                align-items: center;
                justify-content: center;
                transition: .5s;
                display: none;

                @include Sm{
                    display: flex;
                }

                &.open{
                    opacity: 1;
                    visibility: visible;

                    button{
                        position: absolute;
                        left: 0;
                        top: 50%;
                        transform: translateY(-45%);
                        margin-left: 10px;
                        width: 30px;
                        height: 30px;

                        svg{
                            margin-top: 5px;
                        }
                    }

                    input{
                        display: block;
                    }
                }

                .icon{
                    position: absolute;
                    top: 15%;

                    svg{

                        path{
                            fill: #fff;
                        }
                    }
                }

                .cont{
                    position: relative;
                    top: -15%;
                }

                button{
                    width: 100%;
                    height: 100%;
                    background-color: transparent;
                    border: 0;

                    svg{


                        path{
                            fill: #000;
                        }
                    }
                }

                input{
                    border: 0;
                    width: 600px;
                    height: 45px;
                    padding: 0 25px;
                    background-color: transparent;
                    display: none;
                    border-bottom: 1px solid #fff;
                    color: #fff;

                    @include Xs{
                        width: 400px;
                    }

                    @include mob375{
                        width: 290px;
                    }

                    &::placeholder{
                        color: #fff;
                    }
                }
            }
        }

        .close1{
            font-size: 20px;
            color: #84848445;
            cursor: pointer;
            display: flex;
            align-items: center;
        }
    }

    .links{
        display: block;

        .list{
            display: flex;
            align-items: center;
            flex-direction: column;
            height: 100%;
            overflow: auto;

            & > li{
                padding: 7px 15px;
                width: 100%;

                a{
                    width: 100%;
                    display: block;
                    font-size: 16px;
                    color: #000;
                    line-height: 1.6;
                    transition: .5s;                

                    &:hover{
                        color: $color-black;
                    }
                }

                &:not(:last-child){
                    border-bottom: 1px solid #dedede;
                }
            }
        }
    }
}

// Side Nav

// Header
header{
    position: relative;
    min-height: 100vh;
    overflow-y: hidden;

    @include Xs{
        min-height: 80vh;
    }

    @include mob480{
        min-height: 70vh;
    }

    @include mob375{
        min-height: 60vh;
    }

    & > img{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
    }

    .content{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        text-align: center;

        img{

            @include Sm{
                width: 60%;
            }

            @include Xs{
                width: 80%;
            }
        }
    }

    .bottom{
        width: 44px;
        height: 44px;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.149);
        position: absolute;
        bottom: 60px;
        left: 50%;
        transform: translateX(-50%);
        cursor: pointer;

        @include mob480{
            bottom: 30px;
        }

        &:after{
            content: "";
            border-radius: 50%;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(255, 255, 255, 0.149);
            animation: pulse 1s infinite linear;
        }
    }
}

body.home nav{
    padding-top: 38px;
    transition: .5s;

    .container{
        position: relative;

        .lang{
            position: absolute;
            top: -25px;
            left: 15px;
            font-size: 12px;
            color: #bdbdbd;
            transition: .5s;

            &:hover{
                color: #FFF;
            }

            @include Sm{
                display: none;
            }
        }
    }
    
    .content-top{
        display: flex;
        align-items: center;
        overflow: hidden;
    }

    .logo{
        margin-left: auto;

        svg{
            width: 192px;
            height: 61px;
            g{
                fill: #fff;
            }
        }
    }

    .links{
        transition: .5s;

        @include Sm{
            display: none;
        }

        &.open{
            transform: translateX(200px);
            opacity: 0;
            visibility: hidden;
        }

        ul{

            display: flex;


            li{


                a{
                    font-size: 17px;
                    font-family: "frtBold";
                    color: rgb(255, 255, 255);
                    transition: .5s;

                    @include Md{
                        font-size: 15px;
                    }

                    @include Lg{
                        font-size: 17px;
                    }

                    &:hover{
                        color: #bdbdbd;
                    }
                }

                &:not(:last-child){
                    margin-left: 30px;

                    @include Md{
                        margin-left: 20px;
                    }

                    @include Lg{
                        margin-left: 30px;
                    }
                }
            }
        }
    }

    .search{
        margin-right: 45px;

        @include Sm{
            display: none;
        }

        .over{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            visibility: hidden;
            z-index: 99;

            &.open{
                opacity: 1;
                visibility: visible;
            }
        }

        form{
            position: relative;

            & > button{
                padding: 0;
                margin: 0;
                background-color: transparent;
                border: 0;
                transition: .5s;


                &.hide{
                    opacity: 0;
                    visibility: hidden;
                }
            }
        }

        .serach-content{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 200px;
            transition: .5s;

            &.open{
                right: calc(-350px - -31px);
                z-index: 99;
            }

            input{
                width: 350px;
                height: 30px;
                background-color: transparent;
                border: 0;
                border-bottom: 1px solid rgba(255, 255, 255, 0.7);
                padding: 0 10px;
                color: #fff;
                transition: .5s;

                &::placeholder{
                    color: rgb(212, 212, 212);
                }

                &:focus{
                    border-bottom: 1px solid rgba(255, 255, 255, 1);
                }
            }

            button{
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 0;
                padding: 0;
                margin: 0;
                background-color: transparent;
                border: 0;
            }
        }
    }

    .bars{
        width: 30px;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 15px;
        margin-bottom: 7px;
        cursor: pointer;
        display: none;

        @include Sm{
            display: flex;
        }

        span{
            width: 100%;
            height: 2px;
            background-color: #fff;
            position: relative;
            display: block;

            &:after,&:before{
                content: '';
                width: 30px;
                height: 2px;
                position: absolute;
                right: 0;
                background-color: #fff;
            }

            &:before {
                top: 8px;
            }

            &:after {
                top: -8px;
            }
        }
    }

    &.scroll{
        padding: 20px 0 15px;
        margin-top: 0;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 999;
        background-color: #FFF;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
        transition: .5s;

        @include mob480{
            padding: 15px 0;
        }

        .lang{
            top: -15px;

            &:hover{
                color: #000;
            }
        }

        .logo{
            svg{
                width: 192px;
                height: 61px;
                g{
                    fill: #000;
                }
            }
        }

        .links{

            li{

                a{
                    color: #000;
                }
            }
        }

        .search{

            svg{

                path{
                    fill: #000;
                }
            }

            input{
                color: #000 !important;
                border-bottom: 1px solid #000;
            }
        }

        .bars{

            span{
                background-color: #000;


                &:after,&:before{
                    background-color: #000;
                }
            }
        }
    }
}

