// Start Companies

.companies{
    padding: 80px 0;
    background-image: url("../images/clients.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    @include Xs{
        padding: 40px 0;
    }


    .title{
        text-align: center;
        color: #000;

        p{
            font-size: 20px;
            font-family: "frtLight";
            position: relative;
            width: max-content;
            margin: auto;
            margin-bottom: 15px;

            &:after,&:before{
                content: "";
                position: absolute;
                top: 65%;
                display: block;
                width: 20px;
                height: 1px;
                background-color: #c0c0c0;
            }

            &:after{
                right: -40px;
            }

            &:before{
                left: -40px;
            }
        }

        h2{
            font-size: 24px;
            font-family: "frtBlk";
            margin-bottom: 35px;
        }
    }

    .content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        width: 685px;
        margin: auto;

        @include Sm{
            width: 100%;
        }

        @include mob480{
            justify-content: center;
        }


        .company{
            width: 205px;
            height: 240px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-image: url("../images/shape.png");
            background-position: center;
            background-size: cover;
            transition: .5s;

            @include Xs{
                margin: auto;
            }

            img{
                transition: .5s;
                backface-visibility: hidden; 
                -ms-transform: translateZ(0); /* IE 9 */
                -webkit-transform: translateZ(0); /* Chrome, Safari, Opera */
                transform: translateZ(0);
            }

            &.bottom{
                margin-top: 20px;

                @include Xs{
                    margin: auto;
                }
            }

            a{
                margin-top: -30px;
            }

            &:hover{
                transform: scale(1.07);

                @include Xs{
                    transform: scale(1);
                }
            }
        }
    }

    .owl-dots{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        bottom: -20px;

        .owl-dot{

            span{
                display: inline-block;
                width: 10px;
                height: 10px;
                background-color: rgba(0,0,0, .4);
                margin: 2.5px;
            }

            &.active{


                span{
                    background-color: rgba(0,0,0, 1);
                }
            }
        }
    }
}

// End Companies

// Start Clients

.clients{
    padding: 100px 0 90px;

    @include Sm{
        padding-bottom: 50px;
    }

    @include Xs{
        padding: 50px 0;
    }

    .title{
        display: flex;
        justify-content: space-between;
        // align-items: center;
        flex-wrap: wrap;

        & > p{
            font-size: 20px;
            font-family: 'frtLight';
            margin-bottom: 25px;
            padding-right: 20px;
            line-height: 1.2;
            position: relative;
            color: #000;
            line-height: 1.2;

            &:after{
                content: "";
                position: absolute;
                top: 0;
                right: 0;
                width: 1px;
                height: 100%;
                background-color: #c0c0c0;
            }
        }

        a{
            font-size: 16px;
            color: #b5b5b5;
            line-height: 1.2;
            font-family: "frtLight";
            transition: .5s;

            &:hover{
                color: #515151;
            }

            @include mob480{
                margin-top: 5px;
            }
        }

        h2{
            width: 100%;

            p{
                font-size: 24px;
                color: rgb(81, 81, 81);
                line-height: 1.889;
                font-family: "frtBlk";
                color: #000;
                width: 65%;
                line-height: 1.5;
    
                @include Xs{
                    width: 100%;
                }

                @include mob375{
                    font-size: 16px;
                }
            }
        }
    }

    .owl-carousel {
        position: relative;

        .owl-stage{
            position: relative;
        }
        
        .owl-item img {
            width: initial;
            display: block;
            margin: auto;
        }

        .item {
            display: flex;
            align-items: center;
            justify-content: center;

            a{
                position: relative;
                width: 165px;
                height: 166px;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
    
                &:hover{
    
                    img{
                        opacity: 1;
                        filter: unset;
                    }
    
                    p{
                        top: 0;
                        opacity: 1;
                        visibility: visible;
                    }
                }
    
                img{
                    opacity: .6;
                    filter: grayscale(100%);
                    transition: .5s;
                }
    
                p{
                    background-color: $color-black;
                    position: absolute;
                    top: 10px;
                    right: 50%;
                    transform: translateX(50%);
                    font-size: 13px;
                    color: rgb(255, 255, 255);
                    line-height: 1.2;
                    font-family: "frtlight";
 
                    padding: 3px 4px 6px;
                    min-width: 91px;         
                    border-radius: 3px;
                    transition: .5s;
                    text-align: center;
                    opacity: 0;
                    visibility: hidden;
                    
                    &:after{
                        content: "";
                        width: 0;
                        height: 0;
                        display: block;
                        border-width: 5px;
                        border-color: #000 transparent transparent;
                        border-style: solid;
                        top: 100%;
                        position: absolute;
                        right: 50%;
                        transform: translateX(50%);
                    }
                }
            }
        }

        .owl-nav {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            margin-top: 0;
            width: 104%;
            display: flex;
            justify-content: space-between;
            font-size: 40px;
            height: 0;

            @include Md{
                width: 97%;
            }

            @include Lg{
                width: 104%;
            }

            @include Sm{
                display: none;
            }

            button {
                margin-top: 0;
                color: #d8d8d8;
                transition: .5s;

                &:hover {
                    
                    span{
                        border: 1px solid #000;
                    }
                }

                &.owl-prev {
                    margin-right: -25px;

                    span{
                        border-top: 0;
                        border-left: 0;
                        transform: rotate(-45deg);
                    }
                }

                &.owl-next {
                    margin-left: -25px;

                    span{
                        border-bottom: 0;
                        border-right: 0;
                        transform: rotate(-45deg);
                    }
                }

                span{
                    display: block;
                    width: 15px;
                    height: 15px;
                    border: 1px solid #adadad;
                    transition: .5s;
                }
            }
        }

        .owl-dots{
            display: flex;
            justify-content: center;
            align-items: center;
            display: none;

            @include Sm{
                display: flex;
            }

            .owl-dot{

                span{
                    display: inline-block;
                    width: 10px;
                    height: 10px;
                    background-color: rgba(0,0,0, .4);
                    margin: 2.5px;
                }

                &.active{


                    span{
                        background-color: rgba(0,0,0, 1);
                    }
                }
            }
        }
    }
}

