                    color: #000;
                }
            }
        }

        .search{

            svg{

                g{
                    fill: #000;
                }
            }

            input{
                color: #000 !important;
            }
        }

        .bars{

            span{
                background-color: #000;


                &:after,&:before{
                    background-color: #000;
                }
            }
        }
    }

    .logo{
        margin-left: auto;

        svg{
            width: 192px;
            height: 61px;
            g{
                fill: #000;
            }
        }
    }

    .links{
        transition: .5s;

        @include Sm{
            display: none;
        }

        &.open{
            transform: translateX(200px);
            opacity: 0;
            visibility: hidden;
        }

        ul{

            display: flex;


            li{


                a{
                    font-size: 17px;
                    font-family: "frtBold";
                    color: #000;
                    transition: .5s;

                    &:hover{
                        color: #bdbdbd;
                    }
                }

                &:not(:last-child){
                    margin-left: 30px;
                }
            }
        }
    }

    .search{
        margin-right: 45px;

        svg{

            path{
                fill: #000;
            }
        }

        @include Sm{
            display: none;
        }

        .over{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            visibility: hidden;
            z-index: 99;

            &.open{
                opacity: 1;
                visibility: visible;
            }
        }

        form{
            position: relative;

            & > button{
                padding: 0;
                margin: 0;
                background-color: transparent;
                border: 0;
                transition: .5s;


                &.hide{
                    opacity: 0;
                    visibility: hidden;
                }
            }
        }

        .serach-content{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 200px;
            transition: .5s;

            &.open{
                right: calc(-350px - -31px);
                z-index: 99;
            }

            input{
                width: 350px;
                height: 30px;
                background-color: transparent;
                border: 0;
                border-bottom: 1px solid #ccc;
                color: #000;

                &::placeholder{
                    color: rgb(212, 212, 212);
                }
            }

            button{
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 0;
                padding: 0;
                margin: 0;
                background-color: transparent;
                border: 0;
            }
        }
    }

    .bars{
        width: 30px;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 15px;
        margin-bottom: 7px;
        cursor: pointer;
        display: none;

        @include Sm{
            display: flex;
        }

        span{
            width: 100%;
            height: 2px;
            background-color: #000;
            position: relative;
            display: block;

            &:after,&:before{
                content: '';
                width: 30px;
                height: 2px;
                position: absolute;
                right: 0;
                background-color: #000;
            }

            &:before {
                top: 8px;
            }

            &:after {
                top: -8px;
            }
        }
    }
}
