                line-height: 1.2;
                font-family: "frtlight";

                padding: 3px 4px 6px;
                min-width: 91px;         
                border-radius: 3px;
                transition: .5s;
                text-align: center;
                opacity: 0;
                visibility: hidden;
                
                &:after{
                    content: "";
                    width: 0;
                    height: 0;
                    display: block;
                    border-width: 5px;
                    border-color: #000 transparent transparent;
                    border-style: solid;
                    top: 100%;
                    position: absolute;
                    right: 50%;
                    transform: translateX(50%);
                }
            }
        }
    }
}

